body, html, #map-page {
  height: 100%
}

#map-page-header {
  border: 0;
}

#map {
  height: 100%;
}

.ui-content {
  padding: 0;
}

.center {
  text-align: center;
}

.blue {
  background-color: blue;
  color: white;
}

.map-city-block {
  border: 1px solid black;
  height: 100%;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.checkin-photo {
  display: inline-block;
  height: 10%;
  min-height: 32px;
  min-width: 32px;
  background-size: 100% auto;
  background-repeat: no-repeat;
  vertical-align: middle;
}

.checkin {
  height: 20%;
  padding: 0px !important;
  min-height: 32px;
}

.checkin-name {
  display: inline-block;
  vertical-align: middle;
}

.andmore {
  text-align: center !important;
  padding: 0px !important;
}

.count {
  margin-top: 5%;
}

.count-text {
}

.info {
  padding: 10px;
}

.info-header {
  text-align: center;
}

#event-popup {
  min-width: 30%;
}

@media (max-width: 1024px) {
  #event-popup {
    width: 100%;
  }
}

.event-header {
  width: 100%;
}

.wide-header {
  margin-left: 0 !important;
  margin-right: 0 !important;
  padding-left: 5% !important;
  padding-right: 5% !important;
}

.debugpanel-header {
  margin: -16px;
}

.debugpanel-title {
  margin-left: 0px !important;
  margin-right: 0px !important;
}

.red-text {
  color: red;
}

.input-and-button {
  position: relative;
}

.pick-button {
  margin-top: -1px;
}

.user-info {
  height: 50px;
}

.user-photo {
  float:left;
  height: 50px;
  width: 50px;
  display: inline-block;
}

.hidden {
  display: none;
}

.event-pin-red {
  background-image: url("data:image/png;base64,")
}

.event-pin-blue {
  background-image: url("data:image/png;base64,")
}

.event-pin-green {
  background-image: url("data:image/png;base64,")
}

.event-marker {
  position: absolute;
  width: 72px;
  height: 72px;
  cursor: pointer;
}

.current-position {
  position: absolute;
  width: 72px;
  height: 72px;
  cursor: pointer;
  background-image: url("data:image/png;base64,")
}

.event-marker-counter {
  font-size: 34px;
  margin-top: 1px;
  text-align: center;
}

#logger-console {
  height: 99%;
}

#logs-page {
  height: 99%;
}
